	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 6 - La rencontre","texte":{"childMarkdownRemark":{"html":"<p>Je marche calmement sur le bord de la route en direction de la ville qu’on m’avait indiquée. Des voitures passent à côté de moi comme si de rien n’était. Une voiture finit par s’arrêter. Une femme conduit la voiture. Elle me regarde dans les yeux, je la regarde dans les yeux aussi. Elle sourit un bref instant et abaisse la vitre de sa voiture.</p>\n<p>Femme : Besoin d’aller quelque part?<br>\nWamien : Ça dépend où vous allez, je vais à Dais.</p>\n<p>Me disant que je pourrais profiter de la jeune femme pour m’y rendre plus rapidement.</p>\n<p>Femme : Je passe par là, je peux vous y conduire si vous voulez.</p>\n<p>Elle appuye sur un bouton qui déverrouille la portière du passager.</p>\n<p>Wamien : D’accord, merci.</p>\n<p>J’ouvre la porte de la voiture, m’assois et m’attache.</p>\n<p>Femme : Quel est votre nom Monsieur? </p>\n<p>Elle me dit ça en tournant sa tête vers moi, donnant un petit coup avec ses beaux et longs cheveux blonds.</p>\n<p>Wamien : Wamien, et vous?<br>\nClara : Moi c’est Clara. Clara Nairo.<br>\nWamien : Enchanté de faire votre connaissance, Madame Clara Nairo.<br>\nClara : Moi de même Monsieur Wamien.</p>\n<p>La route qu’il restait à faire vers la ville n’était pas très très longue. Nous nous sommes rendus au centre-ville.</p>\n<p>Clara : Heum, Wamien?</p>\n<p>Elle rougit un peu.</p>\n<p>Wamien : Oui?<br>\nClara : Je peux vous laisser mes coordonnées si jamais vous voulez revenir en ville.<br>\nWamien : D’accord.</p>\n<p>Elle sourit, prit un papier, écrit ses coordonnées. Je prends le papier, le range dans ma poche de pantalon et sors de la voiture. Je me retourne et me penche un peu vers elle.</p>\n<p>Wamien : Merci pour le transport.<br>\nClara : Toujours un plaisir d’aider son prochain, répondit-elle en souriant avant de partir faire ses choses.</p>\n<p>Je regarde autour de moi me demandant où je devrais bien aller. Je branche discrètement la clé USB à un petit emplacement au niveau de mon poignet. Le GPS m’indique la direction à prendre pour aller dans le bâtiment. Un bloc apparemment était face à moi. Il m’est donc facile d’y entrer comme si de rien n’était. J’entre et me dirige vers le bloc apparemment de Mickaël. Je passe mes yeux en mode détecteur de chaleur. Il y avait quelqu’un dans l’appartement, un homme, sûr que c’était Mickaël. Je toque à la porte doucement. Je vois la réaction de l’homme, un sursaut. Il se lève et se dirige vers la porte. Il l’ouvre un peu.</p>\n<p>Mickaël : Oui?<br>\nWamien : Bonjour, Monsieur Stephan?<br>\nMickaël : Oui, je peux vous aider?</p>\n<p>Il semblant un peu dérangé, comme si on le coupait dans ce qu’il faisait.</p>\n<p>Wamien : J’aimerais savoir si vous voudriez partir en voyage pour le royaume de Damonia. J’ai gagné un coupon pour y aller, mais je n’en ai pas besoin.<br>\nMickaël : Je ne vous connais pas et non merci, j’y ai déjà vécu.</p>\n<p>J’eus le temps de glisser mon pied pour empêcher la porte de se fermer.</p>\n<p>Wamien : Bon, plus sérieusement, je sais des choses à votre sujet Monsieur Stephan. Vous feriez mieux de coopérer si vous voulez avoir un procès équitable.<br>\nMickaël : Foutez le camp de chez moi!</p>\n<p>L’homme essaie de me donner un coup de poing que j’esquive sans difficulté. Je sors une paire de menottes cachées au niveau de ma ceinture et lui attache un poignet. L’homme recule à la fois surpris et frustré et essaie de me donner un coup de pied. Je le bloque avec mon avant-bras et entre chez lui pour essayer de l’immobiliser au sol. Je le plaque contre un mur et le fais tomber à plat ventre au sol. Je lui attrape les bras et les place dans son dos pour le menotter. Je sors un téléphone et appelle Darakei.</p>\n<p>Wamien : Je l’ai attrapé.<br>\nDarakei : On vient vous chercher où tu peux revenir avec lui sans problème?<br>\nWamien : Venez nous chercher, je dois encore récupérer ses données.<br>\nMickaël : Et avec quel mot de passe?</p>\n<p>Mickaël me coupe la parole en souriant, il n’avait pas l’intention de me le donner.</p>\n<p>Darakei : C’est bon on vient vous chercher.</p>"}},"chapitreAvant":"chapitre-5-la-premiere-mission","chapitreApres":"chapitre-7-les-donnees","nomRoman":"le-premier-cyborg-tome-1","slug":"chapitre-6-la-rencontre","codeChapitre":"H0001C006","equivalentUrl":"/stories/chapter/chapter-6-the-encounter"}},"pathContext":{"slug":"chapitre-6-la-rencontre"}}

